import React, { useState, Component } from 'react'
import { render } from 'react-dom'
import MailchimpSubscribe from 'react-mailchimp-subscribe'
import { EmailContainer, EmailInner } from './styles'

const CustomForm = ({ status, message, onValidated }) => {
  const [email, setEmail] = useState('')
  const submit = () => {
    onValidated({ EMAIL: email })
    setEmail('')
  }

  return (
    <EmailContainer>
      <EmailInner>
        {status === 'sending' && <div style={{ color: 'blue' }}>sending...</div>}
        {status === 'error' && (
        <div
          style={{ color: 'red' }}
          dangerouslySetInnerHTML={{ __html: message }}
        />
        )}
        {status === 'success' && (
        <div
          style={{ color: 'green' }}
          dangerouslySetInnerHTML={{ __html: message }}
        />
        )}
        <input
          style={{
            fontSize: '1rm', width: '200px', borderColor: 'black', padding: '10px',
          }}
          value={email}
          onChange={e => setEmail(e.target.value)}
          type="email"
          placeholder="Email Address"
        />
        <br />
        <button
          type="button"
          style={{
            borderColor: 'black', width: '100%', fontSize: '1rm', padding: '10px', color: 'white', background: 'rgb(0, 50, 0)', cursor: 'pointer',
          }}
          onClick={() => submit()}
        >
          Submit
        </button>
      </EmailInner>
    </EmailContainer>

  )
}


const Email = () => {
  const url = 'https://gmail.us19.list-manage.com/subscribe/post?u=05ae8a7dc2c97805cda60ef6d&id=8e172d8d94'
  return (
    <div style={{ width: '75vw', padding: '20px', backgroundColor: '' }} id="email">
      <EmailContainer>
        <h1 style={{
          marginTop: '0px',
          marginBottom: '0px',
          color: 'black',
          fontWeight: '300',
          textAlign: 'center',
        }}
        >
                Sign up to join our waitlist!
        </h1>
      </EmailContainer>
      <EmailContainer style={{ marginBottom: '30px' }}>
        <hr style={{
          border: '1.5px solid',
          borderColor: 'rgb(0, 0, 0)',
          width: '200px',
        }}
        />
      </EmailContainer>
      <EmailContainer>
        <EmailInner>
          <MailchimpSubscribe
            url={url}
            render={({ subscribe, status, message }) => (
              <CustomForm
                status={status}
                message={message}
                onValidated={formData => subscribe(formData)}
              />
            )}
          />
        </EmailInner>
      </EmailContainer>
    </div>
  )
}

export default Email
